div.mainpage {
    header {
        position: fixed;
        width: 100%;
        background-color: #3a3f48;
        height: 70px;
        z-index: 1000;
        transition: 0.3s height;
        overflow: hidden;
        &.open {
            height: 100vh;
            @media screen and (min-width: 850px) {
                height: 70px;
            }
        }
        .elementscontainer {
            height: 70px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            &.mobile {
                height: auto;
                overflow: hidden;
                border-top: 1px solid #6b6b6b;
                ul {
                    padding: 0;
                    margin: 0;
                    li {
                        border-bottom: 1px solid #494949;
                        padding: 10px 30px;
                        list-style: none;
                        text-align: center;
                        a {
                            font-size: 1em;
                            font-family: jf;
                            color: white;
                            text-decoration: none;
                            &:hover,
                            &:visited {
                                text-decoration: none;
                            }
                        }
                        &:last-child {
                            border: 0;
                        }
                    }
                }
            }
            .right {
                padding: 0 10px;
                width: 50%;
                height: 100%;
                display: flex;
                align-items: center;
                .menu {
                    display: none;
                    margin: 0 10px;
                    @media screen and (max-width: 850px) {
                        display: inline-block;
                    }
                    .nav-icon {
                        width: 40px;
                        height: 25px;
                        position: relative;
                        margin: 10px;
                        transform: rotate(0deg);
                        transition: .5s ease-in-out;
                        cursor: pointer;
                        span {
                            display: block;
                            position: absolute;
                            height: 5px;
                            width: 100%;
                            background: white;
                            border-radius: 9px;
                            opacity: 1;
                            left: 0;
                            transform: rotate(0deg);
                            transition: .25s ease-in-out;
                            &:nth-child(1) {
                                top: 0px;
                            }
                            &:nth-child(2),
                            &:nth-child(3) {
                                top: 10px;
                            }
                            &:nth-child(4) {
                                top: 20px;
                            }
                        }
                        &.open {
                            span:nth-child(1) {
                                top: 18px;
                                width: 0%;
                                left: 50%;
                            }
                            span:nth-child(2) {
                                -webkit-transform: rotate(45deg);
                                -moz-transform: rotate(45deg);
                                -o-transform: rotate(45deg);
                                transform: rotate(45deg);
                            }
                            span:nth-child(3) {
                                -webkit-transform: rotate(-45deg);
                                -moz-transform: rotate(-45deg);
                                -o-transform: rotate(-45deg);
                                transform: rotate(-45deg);
                            }
                            span:nth-child(4) {
                                top: 18px;
                                width: 0%;
                                left: 50%;
                            }
                        }
                    }
                }
                .headercontent {
                    margin: 0 10px;
                    min-width: 180px;
                    height: 45px;
                    display: flex;
                    align-items: center;
                    @media screen and (max-width: 850px) {
                        display: none;
                    }
                    .pic {
                        width: 30%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-color: #1d8386;
                        border-bottom-right-radius: 5px;
                        border-top-right-radius: 5px;
                        img {
                            height: 80%;
                        }
                    }
                    .content {
                        width: 70%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        background-color: #32333a;
                        border-bottom-left-radius: 5px;
                        border-top-left-radius: 5px;
                        p {
                            color: white;
                            font-size: 0.8em;
                            font-weight: bold;
                            font-family: jf;
                        }
                    }
                }
            }
            .left {
                padding: 0 10px;
                width: 50%;
                height: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                @media screen and (max-width: 850px) {
                    display: none;
                }
                .socitem {
                    min-width: 50px;
                    width: 50px;
                    min-height: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    margin: 0 15px;
                    div.soc-img {
                        background-repeat: no-repeat;
                        background-position: center center;
                        width: 100%;
                        height: 100%;
                        border: 3px solid white;
                        border-radius: 50%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        &.google-plus {
                            background-size: auto 50%;
                            background-image: url('/dist/images/google.svg');
                        }
                        &.twitter {
                            background-size: auto 60%;
                            background-image: url('/dist/images/twitter.svg');
                        }
                        &.facebook {
                            background-size: auto 70%;
                            background-image: url('/dist/images/facebook.svg');
                        }
                    }
                }
            }
            &.mobile {
                display: none;
                @media screen and (max-width: 850px) {
                    display: block;
                }
            }
        }
    }
    section {
    	overflow: hidden;
        padding-top: 70px;
        width: 100vw;
        margin: 0 auto;
        min-height: 800px;
        .img-banner {
            margin: 10px 0;
            border-radius: 10px;
            width: 100%;
            height: 410px;
            background-size: 100% auto;
            background-repeat: no-repeat;
            background-position: center center;
            background-image: url(/dist/images/banner2.jpg);
            @media screen and (max-width: 1500px) {
                height: 350px;
            }
            @media screen and (max-width: 1280px) {
                height: 300px;
            }
            @media screen and (max-width: 1024px) {
                height: 260px;
            }
            @media screen and (max-width: 850px) {
                height: 200px;
            }
            @media screen and (max-width: 680px) {
                height: 160px;
            }
            @media screen and (max-width: 680px) {
                display: none;
            }
        }
        .category_search {
            width: 100%;
            ul {
            	margin: 10px 0;
            	padding: 0;
                display: flex;
                align-items: center;
                justify-content: center;
                flex-wrap: wrap;
                @media screen and (max-width: 850px) {
                    display: none;
                }
                li {
                    margin: 5px;
                    list-style: none;
                    min-width: 185px;
                    width: 10%;
                    height: 50px;
                    border-radius: 10px;
                    overflow: hidden;
                    a {
                        height: 100%;
                        background-color: #e84e3d;
                        display: inline-flex;
                        color: white;
                        text-decoration: none;
                        width: 100%;
                        &:hover,
                        &:visited {
                            color: white;
                            text-decoration: none;
                        }
                        &.movies-ara {
                            background-color: #e84e3d;
                        }
                        &.movies-eng {
                            background-color: #636a79;
                        }
                        &.songs-ara {
                            background-color: #2581bc;
                        }
                        &.songs-eng {
                            background-color: #fcb040;
                        }
                        &.series-ara {
                            background-color: #075456;
                        }
                        &.series-eng {
                            background-color: #2581bc;
                        }
                        &.games {
                            background-color: #1d8386;
                        }
                        &.app {
                            background-color: #3ab29c;
                        }
                        &.tv {
                            background-color: #dcb310;
                        }
                        &.wwe {
                            background-color: #342c34;
                        }
                        &.other {
                            background-color: #c74571;
                            .pic {
                                background-size: auto 80%;
                            }
                        }
                        .pic {
                            width: 30%;
                            height: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-bottom-right-radius: 5px;
                            border-top-right-radius: 5px;
                            background-size: 80% auto;
                            background-repeat: no-repeat;
                            background-position: center center;
                        }
                        .content {
	                    	padding: 5px;
                            width: 70%;
                            height: 100%;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            border-bottom-left-radius: 5px;
                            border-top-left-radius: 5px;
                            p {
                                color: white;
                                font-size: 16px;
                                font-weight: bold;
                                font-family: jf;
                                text-align: center;
                            }
                        }
                    }
                }
            }


            .search {
            	margin: 15px auto;
                width: 40%;
                height: 70px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 12px;
                border: 15px solid #3a3f48;
                background-color: #3a3f48;
                @media screen and (max-width: 850px) {
                    display: none;
                }
                form {
                	overflow: hidden;
	                border-radius: 8px;
                    width: 100%;
                    height: 100%;
                    display: flex;
                    flex-direction: row-reverse;
                    justify-content: space-between;
                    .logo {
                    	outline: 0;
                    	padding: 0;
                        width: 40px;
                        height: 40px;
                        background-color: #eb4c82;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        height: 100%;
                        border: 0;
                        display: inline-block;
                        background-position: center center;
                        background-repeat: no-repeat;
                        background-size: 80% auto;
                        background-image: url(/dist/images/search-icon.svg);
                    }
                    input {
                    	flex-grow: 1;
                        background-color: white;
                        width: 90%;
                        border: 0;
                        text-align: center;
                        color: #3a3f48;
                        font-size: 20px;
                        font-weight: bold;
                        font-family: jf;
                        outline: 0;
                    }
                    input::placeholder {
                        color: rgba(58, 63, 72, 0.5);
                        letter-spacing: 0.5px;
                        font-size: 20px;
                        font-weight: bold;
                    }
                }
            }

	        .home_top {
	            width: 100%;
	            display: inline-flex;
	            justify-content: center;
	            align-items: center;
	            margin: 10px;
	            height: 90px;
	            >div{
		            width: 100%;
		            height: 100%;
	            	background-position: center center;
	            	background-repeat: no-repeat;
	            	background-size: auto 100%;
	            }
	            @media screen and (max-width: 850px) {
	                display: none;
	            }
	        }
        }


        .posts-container {
        	display: flex;
        	align-items: flex-start;
            width: 100%;
            padding: 10px;
            a.side-bans {
                display: inline-block;
                justify-content: center;
                align-items: center;
                margin: 10px;
                >div{
	                min-width: 160px;
	                width: 160px;
    	            min-height: 600px;
    	            height: 600px;
                	background-position: center center;
                	background-repeat: no-repeat;
                	background-size: auto 100%;
                }
                @media screen and (max-width: 850px) {
                    display: none;
                }
            }

            .items {
            	margin: 10px;
                width: 100%;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-around;
                .item {
                	border-radius: 15px;
                	overflow: hidden;
                    margin: 10px 4px;
                    border: 2px solid #83BF17;
                    color: black;
                    text-decoration: none;
                    width: 250px;
                    display: flex;
                    flex-direction: column;
                    justify-content: flex-start;
                    background: white;
                    z-index: 4;
                    &:hover,
                    &:visited {
	                    text-decoration: none;
                    }
                    .post-img {
                        background-image: url(../images/items/1.jpg);
                        width: 100%;
                        height: 300px;
                        z-index: 5;
                        background-size: cover;
                        background-repeat: no-repeat;
                    }
                    p {
                    	word-break: break-word;
                        width: 100%;
                        min-height: 67px;
                        z-index: 5;
                        display: block;
                        font-size: 18px;
                        padding: 8px;
                        font-family: cursive;
                        text-align: center;
                    }
                    &.movies-ara {
                        border: 2px solid #e84e3d;
                    }
                    &.movies-eng {
                        border: 2px solid #636a79;
                    }
                    &.songs-ara {
                        border: 2px solid #2581bc;
                    }
                    &.songs-eng {
                        border: 2px solid #fcb040;
                    }
                    &.series-ara {
                        border: 2px solid #075456;
                    }
                    &.series-eng {
                        border: 2px solid #2581bc;
                    }
                    &.games {
                        border: 2px solid #1d8386;
                    }
                    &.app {
                        border: 2px solid #3ab29c;
                    }
                    &.tv {
                        border: 2px solid #dcb310;
                    }
                    &.wwe {
                        border: 2px solid #342c34;
                    }
                    &.other {
                        border: 2px solid #c74571;
                    }
                }
            }
        }
    }

    .pagination-ban{
        width: 100%;
    	a.home_bottom{
            width: 100%;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            margin: 10px;
            height: 90px;
            >div{
	            width: 100%;
	            height: 100%;
            	background-position: center center;
            	background-repeat: no-repeat;
            	background-size: auto 100%;
            }
            @media screen and (max-width: 850px) {
                display: none;
            }
    	}

	    ul.pagination {
            padding: 0;
            width: 100%;
            display: inline-flex;
            justify-content: center;
            margin: 20px;
            li {
                border: 2px solid #3a3f48;
                border-radius: 10px;
                height: 35px;
                width: 35px;
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 2px;
                a, span {
                    color: #3a3f48;
                    height: 100%;
                    width: 100%;
                    display: inline-flex;
                    justify-content: center;
                    align-items: center;
                    font-family: monospace;
                	text-decoration: none;
                    &:hover, &:visited{
                    	color: #3a3f48;
                    	text-decoration: none;
                    }
                }
                &.active {
                    background-color: #3a3f48;
                    span{
	                    color: white;
	                    font-family: jf;
                    }
                }
                &.disabled {
                    span {
                        cursor: not-allowed;
                    }
                }
            }
	    }
    }

    footer {
        width: 100vw;
        background-color: #3a3f48;
        height: 60px;
        padding: 5px;
        .elementscontainer {
            display: flex;
            align-items: center;
            justify-content: flex-end;
            @media screen and (max-width: 850px) {
                justify-content: center;
            }
            .copy {
                width: 370px;
                height: 43px;
                display: flex;
                align-items: center;
                @media screen and (max-width: 850px) {
                    width: 280px;
                }
                .pic {
                    width: 30%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    background-color: #1d8386;
                    border-bottom-right-radius: 5px;
                    border-top-right-radius: 5px;
                    img {
                        height: 80%;
                    }
                }
                .content {
                    width: 70%;
                    height: 100%;
                    display: flex;
                    padding: 10px;
                    align-items: center;
                    justify-content: center;
                    background-color: #32333a;
                    border-bottom-left-radius: 5px;
                    border-top-left-radius: 5px;
                    p {
                        text-align: center;
                        color: white;
                        font-size: 0.6em;
                        line-height: 1.2em;
                        font-weight: bold;
                        font-family: jf;
                    }
                }
            }
        }
    }
}

.insidepage {
