            h1 {
                font-family: jf;
                margin: 0;
                font-size: 1.8em;
                color: white;
                width: 100%;
                height: 100%;
                display: flex;
                align-items: center;
                img {
                    width: 50px;
                    margin: 15px;
                }
            }
            &:last-child {
                padding: 10px 50px;
                justify-content: flex-end;
                img {
                    height: 100%;
                }
            }
        }
        @media screen and (max-width: 850px) {
            height: 160px;
            flex-direction: column-reverse;
            a {
                width: 100%;
                padding: 0;
                justify-content: center;
                h1 {
                    font-size: 1em;
                    justify-content: center;
                    img {
                        width: 30px;
                    }
                }
                &:last-child {
                    padding: 0;
                    justify-content: center;
                }
            }
        }
    }
    footer {
        width: 100vw;
        background-color: #3a3f48;
