            min-width: 300px;
            height: 480px;
            border: 15px solid #3a3f48;
            margin-bottom: 10px;
            border-radius: 20px;
            @media screen and (max-width: 850px) {
                width: 90%;
                height: 350px;
                border-width: 10px;
                border-radius: 10px;
            }
        }
        .share-title {
            display: flex;
            justify-content: space-between;
            align-items: center;
            width: 850px;
            margin-bottom: 15px;
