                justify-content: space-around;
                align-items: center;
                .socitem {
                    width: 50px;
                    height: 50px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    background-repeat: no-repeat;
                    background-size: 90%;
                    background-position: center center;
                    &.google-plus {
                        background-image: url('/dist/images/g.svg');
                    }
                    &.twitter {
                        background-image: url('/dist/images/t.svg');
                    }
                    &.facebook {
                        background-image: url('/dist/images/f.svg');
                    }
                }
                @media screen and (max-width: 850px) {
                    display: none;
                }
            }
        }
